@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap');

/* estilos tipos de pokemon */
:root {
	--color-fire: #ff7402;
	--color-grass: #9bcc50;
	--color-steel: #9eb7b8;
	--color-water: #4592c4;
	--color-psychic: #f366b9;
	--color-ground: #ab9842;
	--color-ice: #51c4e7;
	--color-flying: #3dc7ef;
	--color-ghost: #4d5b64;
	--color-normal: #a4acaf;
	--color-poison: #7e0058;
	--color-rock: #a38c21;
	--color-fighting: #d56723;
	--color-dark: #707070;
	--color-bug: #729f3f;
	--color-dragon: linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);
	--color-electric: #bba909;
	--color-fairy: #fdb9e9;
	--color-ghost: #7b62a3;
	--color-unknow: #757575;

	--color-primary: #006d77;
	--color-primary-hover: #83c5be;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Montserrat';
}
/* Globales */
.container {
	max-width: 1200px;
	margin: 0 auto;
}

/* Header */
header {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	width: 100%;
	padding: 50px 0px;
	position: sticky;
	top: 0;
	background-color: #fff;
	z-index: 2;
}

.logo img {
	width: 250px;
}

header form {
	display: flex;
	align-items: center;
	gap: 50px;
}

.form-group {
	display: flex;
	align-items: center;
	border: 1px solid #555;
	padding: 15px 10px;
	border-radius: 30px;
	width: 100%;
}

.form-group input {
	padding: 0px 10px 0px 10px;
	font-family: inherit;
	width: 260px;
	border: none;
	outline: none;
	font-size: 15px;
}

.icon-search {
	width: 20px;
	height: 20px;
	stroke: #555;
}

.btn-search {
	border: none;
	outline: none;
	border-radius: 30px;
	padding: 12px 30px;
	font-family: inherit;
	font-size: large;
	color: #fff;
	background-color: var(--color-primary);
	cursor: pointer;
}

.btn-search:hover {
	background-color: var(--color-primary-hover);
	color: #000;
	transition: 0.3s;
}

/* Btn Filter */
.container-filter {
	border: none;
	outline: none;
	border-radius: 30px;
	padding: 8px 30px;
	font-family: inherit;
	font-size: large;
	color: #fff;
	background-color: var(--color-primary);
	cursor: pointer;
}

.container-filter:hover{
	background-color: var(--color-primary-hover);
	color: #000;
	transition: 0.3s;
}


.icon-filter {
	display: flex;
	align-items: center;
	gap: 15px;
	cursor: pointer;
}

.icon-filter .icon {
	width: 30px;
	height: 30px;
	stroke: rgb(255, 255, 255);
}

/* Card List Pokemon */
.card-list-pokemon {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 15px;
}

.card-pokemon {
	cursor: pointer;
	text-decoration: none;
}

.card-img {
	background-color: #f2f2f2;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 30px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-img:hover{
	background-color: var(--color-primary);
	filter: drop-shadow(0 0 0.2rem var(--color-primary));
	transition: 0.3s;

}

.card-img img {
	width: 100%;
	height: 100%;
}

.card-img img:hover{
	transform: scale(1.5);
}

.card-info {
	padding: 15px;
}

.card-info h3 {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #333;
	font-size: x-large;
}

.pokemon-id {
	display: block;
	color: #888;
	margin-bottom: 15px;
}

.card-types {
	display: flex;
	gap: 10px;
	margin-top: 15%;
	transform: scale(1.5);
	justify-content: center;
	transition: 0.5s;
}

.card-types:hover{
	transform: scale(2);
	transition: 0.5s;
}

.card-types span {
	font-size: 12px;
	padding: 5px 20px;
	border-radius: 5px;
	color: #fff;
}

/* Types of Pokemon */
.water {
	background-color: var(--color-water);
}

.grass {
	background-color: var(--color-grass);
}

.poison {
	background-color: var(--color-poison);
}

.fire {
	background-color: var(--color-fire);
}

.bug {
	background-color: var(--color-bug);
}

.dragon {
	background: var(--color-dragon);
}

.flying {
	background-color: var(--color-flying);
}

.ground {
	background-color: var(--color-ground);
}

.steel {
	background-color: var(--color-steel);
}

.psychic {
	background-color: var(--color-psychic);
}

.ice {
	background-color: var(--color-ice);
}

.ghost {
	background-color: var(--color-ghost);
}

.normal {
	background-color: var(--color-normal);
}

.rock {
	background-color: var(--color-rock);
}

.electric {
	background-color: var(--color-electric);
}

.fighting {
	background-color: var(--color-fighting);
}

.fairy {
	background-color: var(--color-fairy);
}

.dark {
	background-color: var(--color-dark);
}

/* Container Filters */
.container-filters {
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -300px;
	width: 250px;
	color: #555;
	background-color: #f2f2f2;
	height: 100%;
	padding-top: 50px;
	transition: all 0.3s linear;
	z-index: 3;
}

.container-filters.active {
	left: 0;
	z-index: 3;
}

.filter-by-type {
	display: flex;
	flex-direction: column;
	gap: 30px;
}

.filter-by-type span {
	font-weight: 700;
	font-size: 22px;
}

.group-type {
	display: flex;
	gap: 10px;
	margin-left: 15px;
}

.group-type:hover{
	background-color: var(--color-primary);
	color: rgb(255, 255, 255);
	border-radius: 30px;
	width: 100%;
	margin-left: 30px;
	transition: 0.2s;
}

.group-type label {
	cursor: pointer;
}

/* 
======================================= 
Styles of Pokemon Single Page
======================================= 
*/

.main-pokemon {
	display: flex;
	flex-direction: column;
	margin-top: 100px;
	margin-bottom: 50px;
}

.header-main-pokemon {
	margin-top: 20px;
	display: flex;
	align-items: center;
	position: relative;
}

.number-pokemon {
	position: absolute;
	top: -100px;
	font-size: 250px;
	font-weight: 700;
	color: var(--color-primary-hover);
	opacity: 0.2;
}

.container-img-pokemon {
	order: 2;
	height: 400px;
	width: 400px;
	margin-left: 50px;
	z-index: 4;
}

.container-img-pokemon img {
	height: 125%;
	width: 125%;
	margin-top: -125px;
	transition: 0.5s;
}

.container-img-pokemon img:hover{
	transform: scale(1.5);
	transition: 0.5s;
}

.container-info-pokemon {
	display: flex;
	flex-direction: column;
	order: 1;
	flex: 1;
}

.container-info-pokemon h1 {
	text-align: center;
	font-size: 60px;


}

.info-pokemon {
	display: flex;
	justify-content: space-between;

	margin-top: 20px;
}

.group-info{
	display: flex;
	flex-direction: column;
	gap: 20px;
	font-size: x-large;
}

.group-info p {
	font-weight: 700;
	font-size: 20px;
}

/* Stats */
.container-stats {
	display: flex;
	gap: 50px;
	align-items: center;
}

.stats {
	display: flex;
	flex-direction: column;
	gap: 20px;
	flex: 1;
}

.stat-group {
	display: flex;
	align-items: center;
	gap: 20px;
}

.stat-group span {
	flex: 20%;
	font-weight: 500;
	font-size: 18px;
}

.progress-bar {
	width: 100%;
	height: 30px;
	background-color: var(--color-primary);
	border-radius: 10px;
}
/* Search Page */

.p-search{
	margin-bottom: 30px;
	font-size: 22px;
}

.p-search span{
	font-weight: 700;
}

/* Loader */

.container-loader{
	display: flex;
	justify-content: center;
	margin: 200px 0;
}

/* Btn Load More */

.container-btn-load-more{
	display: flex;
	justify-content: center;
	margin-bottom: 20px;
	margin-top: 20px;
}

.btn-load-more{
	border: none;
	padding: 15px 40px;
	font-family: inherit;
	background-color: var(--color-primary);
	color: #fff;
	border-radius: 30px;
	cursor: pointer;
}

.btn-load-more:hover{
	background-color: var(--color-primary-hover);
	color: #000;
	transition: 0.3s;
}

.btn-flotante {
	font-size: 16px;
	font-family: inherit;
	text-transform: uppercase;
	font-weight: bold;
	color: #ffffff;
	border-radius: 30px;
	background-color: var(--color-primary);
	padding: 18px 30px; /* Relleno del boton */
	position: fixed;
	bottom: 40px;
	right: 40px;
	transition: all 300ms ease 0ms;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
	z-index: 99;
	text-decoration: none;
}
.btn-flotante:hover {
	background-color: var(--color-primary-hover);
	box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3);
	transform: translateY(-7px);
	color: #000;
}